<template>
  <main>
    <div
      class="twoColumns container pt-lg-23 pb-lg-20 pt-md-16 pb-md-4 pt-10 pb-4"
    >
      <div class="row">
        <div class="col-12 col-lg-9 order-lg-3">
          <article id="content">
            <blog-article v-for="post in posts" :post="post" :key="post.id" />
            <pagination />
          </article>
        </div>
        <blog-side-bar />
      </div>
    </div>
  </main>
</template>

<script>
import { mapState } from "vuex";
export default {
