<script>
import { mapState } from "vuex";
export default {
  props: ["post"],
  data(){
    return{
      document
    }
  },
  mounted() {
    // console.log(this.$route);
    if (!this.post) {
      if (this.$route.params.id)
        this.$store.dispatch("post/show", { id: this.$route.params.id });
      else this.$router.push("/");
    }
    this.$store.dispatch("post/index");
  },
  computed: {
    ...mapState({
      post_by_id: (state) => state.post.one,
      posts: (state) => state.post.all,
    }),
  },
  watch: {
    post_by_id(val) {
      this.post = val;
    },
  },
};
</script>

