import RelatedProducts from "../components/product/RelatedProducts.vue";
export default {
  components: { ProductPageSlider, ProductSlider, RelatedProducts },
  mounted() {
    let id = this.$route.params.id;
    this.$store.dispatch("item/show", { id });
